body {
  margin: 0;
  padding: 0;
  font-family: Arial, sans-serif;
}

.people {
  grid-template-columns: repeat(auto-fill, minmax(200px, 1fr));
  justify-content: center;
  gap: 40px 30px;
  padding: 30px 35px;
  display: grid;
}

.people .person {
  width: 100%;
  max-width: 200px;
}

@media screen and (width <= 499px) {
  .people {
    grid-template-columns: repeat(2, 200px);
    min-width: 500px;
  }
}

@media screen and (width >= 500px) {
  .people {
    grid-template-columns: repeat(2, 200px);
    min-width: 500px;
  }
}

@media screen and (width >= 1000px) {
  .people {
    grid-template-columns: repeat(4, 200px);
  }
}

.person {
  color: #000;
  flex-direction: column;
  align-items: center;
  padding: 10px 10px 9px;
  display: flex;
  position: relative;
  overflow: hidden;
}

.person:hover {
  background-color: #eee;
  border-radius: 60px;
}

.photo:hover img {
  border-radius: 60px;
  width: 240px;
  height: 240px;
}

.person .extra {
  display: none;
}

.person:hover .extra {
  justify-content: center;
  align-items: center;
  margin-top: 10px;
  display: flex;
  position: fixed;
}

.photo {
  position: relative;
  overflow: hidden;
}

.photo img {
  border-radius: 200px;
  width: 200px;
  height: 200px;
  transition: width .3s, height .3s, border-radius .3s;
  display: block;
}

.rank {
  color: #8b0000;
  text-align: center;
  background-color: #fff;
  border-radius: 32px;
  width: 32px;
  height: 32px;
  padding: 4px;
  line-height: 32px;
  position: absolute;
  top: 50%;
  right: 10px;
  transform: translateY(-50%);
}

.details {
  color: #000;
  padding: 10px;
}

.name {
  color: #000;
  font-size: 18px;
  text-decoration: none;
  transition: color .3s;
}

.name:hover {
  color: #ffc107;
}

.sport, .pay {
  color: #666;
  font-size: 14px;
}
